Bénévoles + 02 100 Emplois + 230 Radios + 80 Départements + 12 Fédérations Nos auditeurs : Selon la source Médiamétrie en Novembre et Décembre 2018 : - Audience cumulée : 2.1% soit 1 165 000 auditeurs (567 stations) - Part d'audience : 1,6% - Durée d'écoute moyenne 1H42 Cette tendance se confirme d’année en année et nous conforte dans le paysage audiovisuel français. Parmi les fédérations régionales et nationales adhérentes : ARRA : Assemblée Régionale des Radios Associatives // Occitanie – Pyrénées Méditerranée CRAL : Coordination des Radios associatives de Lorraine FRALA : Fédération des Radios Associatives du Limousin en Aquitaine FFRC : Fédération Française des Radios Chrétiennes FRAMA : Fédération Régionale des Radios Associatives Maine Anjou FARA : Fédération Alsacienne des Radios Associatives FRADIF : Fédération des Radios Associatives d’Ile de France FRANC-CP : Fédération Régionale des Radios Associatives Non Commerciales Charente-Poitou HAUTS DE FRANCE : Fédération des Radios Associatives du Nord de la France AURA FM : Fédération Régionale des Radios Auvergne Rhône-Alpes FRAD’AUV : Fédération des Radios Associatives d’Auvergne FRAN : Fédération des Radios Associatives de Normandie Contact : Vous avez besoin de joindre la Confédération Nationale des Radios Associatives, ou vous souhaitez rejoindre le projet Taradio, vous pouvez nous contacter : Courrier : C/O RADIO ALPA MJC Jacques Prévert 97 Grande Rue, 72 000 LE MANS Courriel : president@cnra.fr ")])]),_c('div',{class:_vm.showFilter? 'filter-wrapper':'filter-wrapper faded'},[_c('input',{directives:[{name:"model",rawName:"v-model",value:(_vm.filters.distance),expression:"filters.distance"}],attrs:{"type":"number"},domProps:{"value":(_vm.filters.distance)},on:{"change":function($event){return _vm.updateFilters(_vm.filters)},"input":function($event){if($event.target.composing){ return; }_vm.$set(_vm.filters, "distance", $event.target.value)}}})])])}
